html, body {
    position:absolute;
    top:0;
    bottom:0;
}

* {
    -webkit-tap-highlight-color: rgba(0,0,0,0); /* make transparent link selection, adjust last value opacity 0 to 1.0 */
    box-sizing:border-box;
}

:root {
    --white:#FEFEFE;
    --faint:#BDC3C7;
    --light:#D9CB9E;
    --main:#DC4D01;
    --mid:#374140;
    --shaded:#0F0500;
    --dark:#2A2C2B;

    --emphasis:#EE7F7E;
    --notice:#0F7110;

    --alert:#0190DC;

    --spaceSM:0.2em;
    --spaceBG:2em;
    --spaceMD:0.8em;
}

body {
    /*-webkit-touch-callout: none;                /* prevent callout to copy image, etc when tap to hold */
    /*-webkit-text-size-adjust: none;             /* prevent webkit from resizing text to fit */
    /*-webkit-user-select: none;                  /* prevent copy paste, to allow, change 'none' to 'text' */
    font-family: system-ui, -apple-system, -apple-system-font, 'Segoe UI', 'Roboto', sans-serif;
    font-size:12px;
    height:100%;
    margin:0px;
    padding:0px;
    /* Padding to avoid the "unsafe" areas behind notches in the screen */
    padding: env(safe-area-inset-top, 0px) env(safe-area-inset-right, 0px) env(safe-area-inset-bottom, 0px) env(safe-area-inset-left, 0px);
    width:100%;
}

/* Portrait layout (default) */
.app {
    position:absolute;             /* position in the center of the screen */
    left:0;
    top:0;
    height:100vh;                   /* text area height */
    width:100vw;                   /* text area width */
    text-align:center;
    background:#fff;                                   /* offset horizontal: half of text area width */
    color:#000;
}

/* Landscape layout (with min-width) */
@media screen and (min-aspect-ratio: 1/1) and (min-width:400px) {
    .app {
        /* offset horizontal: half of image width and text area width */
    }
}


button.action img {
    height: 100%;
    position: relative;
}

button.action {
    cursor:pointer;
    height: 3em;
    margin:0.5em;
    display: inline-block;
    position:relative;
}






h1 {
    font-size:24px;
    font-weight:normal;
    margin:0px;
    overflow:visible;
    padding:0px;
    text-align:center;
}

@media screen and (prefers-color-scheme: dark) {
    body {
        background-image:linear-gradient(to bottom, #585858 0%, #1B1B1B 51%);
    }
}

#menu {height:10%;}
#menu button {
    height: 100%;
    max-width: 16vw;
    background: var(--white);
    padding: 1%;
    margin: 0 0.5%;
    border: none;
    border-bottom: 1px solid;
    border-right: 1px solid;
    border-color:var(--main);
    box-sizing:border-box;
    min-width: 18%;
    vertical-align: bottom;
}

#nft-mint {

    font-size: 3em;
    border-radius: 1em;
    padding: 0.2em 1em;
    box-shadow: 0 0 5px 2px var(--alert);
    cursor: pointer;
    /* position: relative; */
    margin: 1em auto;
    display: block;
    box-sizing: border-box;
}

form select, form label, form input, form button {
    display: inline-block;
    height: 3rem;
    line-height:3rem;
    font-size:1rem;
    border-radius:0.4rem;            
    box-sizing: border-box;
    text-align: left;
    vertical-align: bottom;
    margin-bottom: 1rem;
}

form button, button {
    background: var(--white);
    border-radius: 0.4rem;
    text-align:center;
}

form label { padding:0 0.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

*[class^="col"] {
    display: inline-block;
}

.col-1 {width:8.3%;}
.col-2 {width:16.6%;}
.col-3 {width:25%;}
.col-4 {width:33.3%;}
.col-5 {width:41.6%;}
.col-6 {width:50%;}
.col-7 {width:58.3%;}
.col-8 {width:66.6%;}
.col-9 {width:75%;}
.col-10 {width:83.3%;}
.col-11 {width:91.6%;}
.col-12 {width:100%;}

.toast {
    border-radius: 2em;
    font-size:1.2em;
    display: block;
    position:fixed;
    text-align:center;
    width:auto;
    max-width:70%;
    transform:translateX(-50%);
    left: 50%;
    bottom: 10%;
    margin: auto;
    border: 1px solid #000;
    padding: 0.5em;
    background: rgba(0,0,0,0.7);
    color: #fff;
    font-weight: bold;
    -moz-box-shadow: 0 0 5px 2px #2a2a2a;
    box-shadow: 0 0 5px 2px #2a2a2a;
}



.nft {
    overflow:hidden;
    margin:0.5em;
    display:inline-block;
    border: 1px solid;
    width: 12em;
    height: 12em;
    border-radius: 0.5em;
box-shadow: 0 0 4px 1px var(--shaded);
}

.nft > img { width:100%; } 


