#modal {
    width: 100%;
    height: 100%;
    margin:0;
    z-index: 10;
    background: var(--dark);
    position: fixed;
    top: 0;
    right: 0;
}

#modal-inner {
    background: var(--white);
    width: 80%;
    max-width: 1080px;
    margin: auto;
    top:50%;
    border-radius:0.5em;
    left:50%;
    padding:2em;
    position:absolute;
    max-height: 75%;
    overflow:auto;
    transform: translate(-50%,-48%);
}

#modal-inner #close {
    position: absolute;
    top:0;
    right:0;
    font-size:1,1em;
    padding:0.4em;
    cursor:pointer;
    opacity:0.7;
}



